\n\n@keyframes dr-icon-bob-float {\n    100% {\n        -webkit-transform: translateY(-6px);\n        transform: translateY(-6px);\n    }\n}\n\n.dr-icon-bob {\n    display: inline-block;\n    vertical-align: middle;\n    -webkit-transform: perspective(1px) translateZ(0);\n    transform: perspective(1px) translateZ(0);\n    -webkit-transition-duration: 0.3s;\n    transition-duration: 0.3s;\n}\n\n.dr-icon-bob .dr-icon {\n    -webkit-transform: translateZ(0);\n    transform: translateZ(0);\n}\n\n.dr-icon-bob:hover .dr-icon,\n.dr-icon-bob:active .dr-icon {\n    -webkit-animation-name: dr-icon-bob-float, dr-icon-bob;\n    animation-name: dr-icon-bob-float, dr-icon-bob;\n    -webkit-animation-duration: .3s, 1.5s;\n    animation-duration: .3s, 1.5s;\n    -webkit-animation-delay: 0s, .3s;\n    animation-delay: 0s, .3s;\n    -webkit-animation-timing-function: ease-out, ease-in-out;\n    animation-timing-function: ease-out, ease-in-out;\n    -webkit-animation-iteration-count: 1, infinite;\n    animation-iteration-count: 1, infinite;\n    -webkit-animation-fill-mode: forwards;\n    animation-fill-mode: forwards;\n    -webkit-animation-direction: normal, alternate;\n    animation-direction: normal, alternate;\n}\n\n.dr-size-logo {\n    width: 50px;\n}\n\n.scrollbar {\n    float: left;\n    height: 89%;\n    width: 100%;\n    overflow-y: scroll;\n}\n\n@media(max-width: 800px) {\n    .scrollbar {\n        height: 88.5%;\n    }\n}\n\n@media(max-width: 500px) {\n    .scrollbar {\n        height: 90%;\n    }\n}\n\n@media(min-width: 1400px) {\n    .scrollbar {\n        height: 94%;\n    }\n}\n\n.force-overflow {\n    min-height: 450px;\n}\n\n/*\n *  STYLE 1\n */\n\n#style-1::-webkit-scrollbar-track {\n    scrollbar-shadow-color: inset 0 0 6px rgba(0, 0, 0, 0.3);\n    border-radius: 10px;\n    background-color: #a7a7a74d;\n}\n\n#style-1::-webkit-scrollbar {\n    width: 12px;\n    background-color: #a7a7a74d;\n}\n\n#style-1::-webkit-scrollbar-thumb {\n    border-radius: 10px;\n    scrollbar-shadow-color: inset 0 0 6px rgba(0, 0, 0, .3);\n    background-color: #555;\n}\n/*# sourceMappingURL=data:application/json;base64, */"